const container=document.getElementById('root')

const ajax = new XMLHttpRequest();
const content=document.createElement('div')
const NEWS_URL ='https://api.hnpwa.com/v0/news/1.json';
const CONTENT_URL='https://api.hnpwa.com/v0/item/@id.json';
const store={
    currentPage:1,
}


function getDate(url){ // ajax Ìï®Ïàò
    ajax.open('GET' ,url,false) //ÎèôÍ∏∞Ï†Å Ï≤òÎ¶¨
    ajax.send();//Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞;
    return JSON.parse(ajax.response);//json ÏùÑ Í∞ùÏ≤¥Î°ú

}


function newsFead(){ //Í∏Ä Î™©Î°ù Ìï®Ïàò
    const newsFeed=getDate(NEWS_URL); 
    const newsList = [];
    let template = `
        <div>
        <h1> Hacker News</h1>
        <ul>
        {{__news_feed__}}
        </ul>
        <div>
        <a href = "#/page/{{__prev_page__}}">Îã§Ïùå ÌéòÏù¥ÏßÄ</a>
        <a href = "#"/page/{{__next_page__}}>Ïù¥Ï†Ñ ÌéòÏù¥ÏßÄ</a>
        </div>
        </div>
    `;


    for(let i =(store.currentPage - 1) * 10; i < store.currentPage * 10; i++){
    newsList.push (`
    <li>
        <a href = '#/show/${newsFeed[i].id}'>
        ${newsFeed[i].title} (${newsFeed[i].comments_count})
        </a>
    </li>`
    );
}

template = template.replace('{{__news_feed__}}',newsList.join('')); //ÎßàÌÇπ ÎêúÎ∂ÄÎ∂Ñ ÍµêÏ≤¥
template = template.replace('{{__prev_page__}}',store.currentPage > 1 ? store.currentPage -1 : 1); //ÎßàÌÇπ ÎêúÎ∂ÄÎ∂Ñ Íµê
template = template.replace('{{__next_page__}}',store.currentPage + 1); //ÎßàÌÇπ ÎêúÎ∂ÄÎ∂Ñ Íµê
//html ÌïòÎã®Ïóê ÏûêÏãù ÎÖ∏ÎìúÎ°ú Ï∂îÍ∞Ä
container.innerHTML = template; //Î∞∞Ïó¥Ïóê ÏûàÎäî Î¨∏ÏûêÏó¥ÏùÑ ÌïòÎÇòÎ°ú ÎßåÎì†Îã§ (join)
}

function newsDetail(){
    //loaction Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Ï†úÍ≥µÌï¥Ï£ºÎäî Í∞ùÏ≤¥ ->Ï£ºÏÜåÏôÄ Í¥ÄÎ†®Îêú Îã§ÏñëÌïú Ï†ïÎ≥¥Î•º Ïïå Ïàò ÏûàÎã§
const id = location.hash.substr(7);//#Ï†úÍ±∞
const newsCotent=getDate(CONTENT_URL.replace('@id',id))

container.innerHTML=`
    <h1> ${newsCotent.title} </h1>
        <div>
            <a href ="#/page/${store.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
        </div>
`;
}
function router(){
    const routePath = location.hash;
    if(routePath === ''){
        newsFead();
    }else if(routePath.indexOf('#/page/' >= 0)){
        store.currentPage =Number(routePath.substr(7));
        newsFead();
    }else{
        newsDetail();

    }
}

window.addEventListener('hashchange',router)

router();